@charset "utf-8";
/* 禁用iPhone中Safari的字号自动调整 */
html {
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  /* 解决IOS默认滑动很卡的情况 */
  -webkit-overflow-scrolling : touch; 
}
html,body{
  overflow-x: hidden;
}
/* 禁止缩放表单 */
input[type="submit"], input[type="reset"], input[type="button"], input {
  resize: none;
  border: none;
}
/* 解决input在ios下边框重影 */
input{
  border-radius: 0;
  resize: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
/* 去除input内阴影select默认标签样式 */
input,select{
  background-color: transparent;
  border-color: transparent;
}
select::-ms-expand {display: none;}
select{
  -moz-appearance: none;
  -webkit-appearance: none;
}
/* 取消链接高亮  */
a {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
/* 设置HTML5元素为块 */
article, aside, details, figcaption, figure, footer, header, menu, nav, section {
  display: block;
}
/* 图片自适应 */
img {
  width: 100%;
  height: auto;
  width: auto\9; /* ie8 */
  display: block;
  -ms-interpolation-mode: bicubic;/*为了照顾ie图片缩放失真*/
}
/* 初始化 */
body, div, ul, li, ol, h1, h2, h3, h4, h5, h6, input, textarea, select, p, dl, dt, dd, a, img, button, form, table, th, tr, td, tbody, article, aside, details, figcaption, figure, footer, header, menu, nav, section {
  margin: 0;
  padding: 0;
}
body {
  font: 12px/1.5 'STHeiti Light [STXihei]','SimHei';
  color: #555;
  background-color: #fff;
}
em, i,b {
  font-style: normal;
}
ul,li,ol{
  list-style-type: none;
  list-style: none;
  padding:0;
  margin:0;
}
strong {
  font-weight: normal;
}
.clearfix:after {
  content: "";
  display: block;
  visibility: hidden;
  height: 0;
  clear: both;
}
.clearfix {
  zoom: 1;
}
a {
  text-decoration: none;
  color: #5c5c5c;
  font-family: 'STHeiti Light [STXihei]';
}
a:hover {
  text-decoration: none;
}
h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-family: 'STHeiti Light [STXihei]';
  font-weight: bold;
}
img {
  border: none;
}
input{
  font-family: 'STHeiti Light [STXihei]';
}
/*单行溢出*/
.one-txt-cut{
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
/*多行溢出 手机端使用*/
.txt-cut{
  overflow : hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
